html,
body{
  width: 100%;
  height: 100%;
  background-color: #EEE;
  min-height: 100%;

}
body{
  overflow-x: hidden;
}
html{
  font-size: 16px;
}
@media (max-width: 470px){
  html{
    font-size: 14px !important;
  }
}



a{
  text-decoration: none;
  color: inherit;
}
p,h3{
  margin: 0;
}