form {
  color: #777;

  .common_input {
    color: #6d6e71;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    outline: none;
    display: block;
    width: 50%;
    margin-top: 10px;
    padding: 7px;
    border: 1px solid #ddd;
    background: transparent;
    margin-bottom: 10px;
    font-size: 1rem;
  }
  /* Change the white to any color ;) */
  .common_input:-webkit-autofill,
  .common_input:-webkit-autofill:hover,
  .common_input:-webkit-autofill:focus,
  .common_input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px #EEEEEE inset !important;}


  .send_btn {
    background: #27AAE1;
    color: #FFFFFF;
  }

  .send_btn:hover {
    background: #149AC9;
    transition: all 1s;
  }
  @supports(-webkit-appearance: none) or (-moz-appearance: none) {
    input[type='checkbox'] {
      --active: #27AAE1;
      --active-inner: #fff;
      --focus: 2px rgba(41, 190, 247, 0.3);
      --border: #BBC1E1;
      --border-hover: #149AC9;
      --background: #fff;
      --disabled: #F6F8FF;
      --disabled-inner: #E1E6F9;
      -webkit-appearance: none;
      -moz-appearance: none;
      height: 21px;
      outline: none;
      display: inline-block;
      vertical-align: top;
      position: relative;
      margin: 0;
      cursor: pointer;
      border: 1px solid var(--bc, var(--border));
      background: var(--b, var(--background));
      transition: background .3s, border-color .3s, box-shadow .2s;
      &:after {
        content: '';
        display: block;
        left: 0;
        top: 0;
        position: absolute;
        transition: transform var(--d-t, .3s) var(--d-t-e, ease), opacity var(--d-o, .2s);
      }
      &:checked {
        --b: var(--active);
        --bc: var(--active);
        --d-o: .3s;
        --d-t: .6s;
        --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
      }
      &:disabled {
        --b: var(--disabled);
        cursor: not-allowed;
        opacity: .9;
        &:checked {
          --b: var(--disabled-inner);
          --bc: var(--border);
        }
        & + label {
          cursor: not-allowed;
        }
      }
      &:hover {
        &:not(:checked) {
          &:not(:disabled) {
            --bc: var(--border-hover);
          }
        }
      }
      &:focus {
        box-shadow: 0 0 0 var(--focus);
      }
      &:not(.switch) {
        width: 21px;
        &:after {
          opacity: var(--o, 0);
        }
        &:checked {
          --o: 1;
        }
      }
      & + label {
        font-size: 14px;
        line-height: 21px;
        display: inline-block;
        vertical-align: top;
        cursor: pointer;
        margin-left: 4px;
      }
    }
    input[type='checkbox'] {
        border-radius: 7px;
        &:after {
          width: 5px;
          height: 9px;
          border: 2px solid var(--active-inner);
          border-top: 0;
          border-left: 0;
          left: 7px;
          top: 2.5px;
          transform: rotate(var(--r, 20deg));
        }
        &:checked {
          --r: 43deg;
        }
      }


    }

}

.input_error{
  border: 1px solid red;
}
.error{
  color:red;
}
.span_error{
  color: firebrick;
}